@use "sass:math";
@import "../../../includes/common-styles/gutenberg-responsive-breaks";

.is-mobile-preview .wp-block-group[data-hide-on-mobile] {
	&.wp-block:before {
		content: "This block is hidden on mobile.";
		display: block;
		text-align: center;
		font-size: 0.8em;
		border: 1px dashed #ddd;
		color: #999;
		padding: 1em;
	}
	&:not(.wp-block),
	& > * {
		display: none !important;
	}
}

.is-tablet-preview .wp-block-group[data-hide-on-tablet]  {
	&.wp-block:before {
		content: "This block is hidden on tablet.";
		display: block;
		text-align: center;
		font-size: 0.8em;
